import { useDispatch } from 'react-redux';

import styled from 'styled-components';

const StyledConfigurationStartInput = styled.div`
  width: 300px;
  height: 47px;
  padding: 14px 20px 14px 26px;
  border: 1px dashed rgba(0, 0, 0, 0);
  background: #F7F7F7;
  input {
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    background: #F7F7F7;
  }
`

const SizeInputItem = ({name, value, func, error}) => {
  const dispatch = useDispatch();

  const onChangeValue = (curentValue) => {
    dispatch(func(curentValue));
  }

  return (
    <StyledConfigurationStartInput >
      <input 
        type="number"
        required
        placeholder={`${name} в мм`}
        value={value} 
        onChange={e => onChangeValue(e.target.value)}
        />
    </StyledConfigurationStartInput>
  )
}

export default SizeInputItem;