import { useSelector, useDispatch } from 'react-redux';
import { setConfiguration } from './../../actions/index';

import styled from 'styled-components';

import ConfigurationСhoiceItem from './configurationСhoiceItem/ConfigurationСhoiceItem';

import { img01, img02, img03, img01Active, img02Active, img03Active} from './../../resources/img/configurationStart';

export const StyledConfigurationStartTitle = styled.h1`
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #212121;
  margin: 50px 0 0;
`
const StyledConfigurationStartList = styled.ul`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 30px 0 0;
`

const ConfigurationСhoice = () => {
  const dispatch = useDispatch();

  const onSetConfiguration = (value) => {
    dispatch(setConfiguration(value))
  } 
    
  return (
    <>
      <StyledConfigurationStartTitle>Выберите конфигурацию</StyledConfigurationStartTitle>
      <StyledConfigurationStartList className="configuration-start__list">
        <ConfigurationСhoiceItem name='Прямая' img={img01} imgActive={img01Active} onSetConfiguration={onSetConfiguration}/>
        <ConfigurationСhoiceItem name='Паралельная' img={img02} imgActive={img02Active} onSetConfiguration={onSetConfiguration}/>
        <ConfigurationСhoiceItem name='Угловая' img={img03} imgActive={img03Active} onSetConfiguration={onSetConfiguration}/>
        <ConfigurationСhoiceItem name='Островок' img={img01} imgActive={img01Active} onSetConfiguration={onSetConfiguration}/>
      </StyledConfigurationStartList>
    </>
  );
};

export default ConfigurationСhoice;