import styled from 'styled-components';

const StyledHalf = styled.div`
  flex: 0 0 130px;
  margin: 10px 0 0;
`
const StyledName = styled.span`
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  opacity: 0.87;
` 
const StyledInput = styled.input`
  width: 100%;
  background: #F8F8F8;
  border-radius: 2px;

  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #212121;
  opacity: 0.9;
  padding: 8px 12px;
  margin: 6px 0 0;
`

const ModuleParametersSizeItem = ({name, lovalValue, localFuncChange, globalFuncChange}) => {

  return (
    <StyledHalf>
      <StyledName>{name}</StyledName>
      <StyledInput
        value={lovalValue}
        onChange={e => localFuncChange(e.target.value)} 
        onKeyDown={e => globalFuncChange(e.code)}
        onBlur={() => globalFuncChange('Enter')}
        type="number" />
    </StyledHalf>
  );
};

export default ModuleParametersSizeItem;