import { StyledTableCalcDetailsRow, StyledTableCalcDetailsItem } from './../../table/tableCalcDetails/TableCalcDetails';

import cherryPng from './../cherry.png';
import eichesilPng from './../eichesil.png';

const materialImg = {
  'Cherry': cherryPng,
  'Eichesil': eichesilPng
}

const TableItemCalcDetails = ({i, k, item, rectangle}) => {
  return (
    <StyledTableCalcDetailsRow>
      <StyledTableCalcDetailsItem flex={42}>{i===0?k:''}</StyledTableCalcDetailsItem>
      <StyledTableCalcDetailsItem flex={130}>{i===0?item.name:''}</StyledTableCalcDetailsItem>
      <StyledTableCalcDetailsItem flex={130}>
        <img src={materialImg[item.material]} alt="Не выбран" />
        <span>{item.material}</span>
      </StyledTableCalcDetailsItem>
      <StyledTableCalcDetailsItem flex={58} marginRight={20}>{item.x}</StyledTableCalcDetailsItem>
      <StyledTableCalcDetailsItem flex={58} marginRight={20}>{item.y}</StyledTableCalcDetailsItem>
      <StyledTableCalcDetailsItem flex={100} textAlign={'center'}>{item.quantity}</StyledTableCalcDetailsItem>
      <StyledTableCalcDetailsItem flex={125}>
        <img src={rectangle} alt="Кромка" />
        <span>{item.edge?item.edge:'Не выбрана'}</span>
      </StyledTableCalcDetailsItem>
    </StyledTableCalcDetailsRow>
  );
};

export default TableItemCalcDetails;