import { useSelector, useDispatch } from 'react-redux';
import { setConfigurationPage, setSizeError, setHeightU, setHeightD } from './../../../../actions/index';

import NavStart from './../../../nav/navStart/NavStart';
import ConfigurationСhoice from './../../../configurationСhoice/ConfigurationСhoice';
import SizeInput from './../../../sizeInput/SizeInput';

import styled from 'styled-components';

import StyledConteiner from '../../../styledGlobal/StyledConteiner';

const StyledConfigurationStart = styled.section`
  padding: 0 0 70px;
`

const ConfigurationStart = () => {
  const { widthA, widthB, depth, height, apron, configuration} = useSelector(state => state);
  const dispatch = useDispatch();

  const onSetPage = () => {
    if (!(widthA > 0) || !(depth > 0) || !(height > 0) || !(apron > 0) || (!(widthB > 0) && configuration === 'Угловая'))
      dispatch(setSizeError(true));
    else {
      const newHeight = Math.trunc((height - apron) / 2)
      dispatch(setHeightU(newHeight))
      dispatch(setHeightD(newHeight))
      dispatch(setConfigurationPage(2))
    }
  }

  return (
    <>
      <NavStart onSetPage={onSetPage}/>
      <main>
        <StyledConfigurationStart>
          <StyledConteiner className="configuration-start__content _container">
            <ConfigurationСhoice />
            <SizeInput />
          </StyledConteiner>
        </StyledConfigurationStart>
      </main>
    </>
  );
};

export default ConfigurationStart;