import { useSelector } from 'react-redux';
import { setWidthA, setWidthB, setDepth, setHeight, setApron} from '../../actions';

import styled from 'styled-components';

import SizeInputItem from './sizeInputItem/SizeInputItem';

import { StyledConfigurationStartTitle } from './../configurationСhoice/ConfigurationСhoice';

const StyledConfigurationStartRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: ${props => props.marginTop || '30px'};
`

const SizeInput = () => {
  const { widthA, depth, height, widthB, apron, configuration} = useSelector(state => state);

  let widthWallAName = 'Длина';
  let widthWallBView = null;
  if (configuration==='Угловая') {
    widthWallAName = 'Длина стены "A"'
    widthWallBView = <SizeInputItem name='Длина стены "B"' value={widthB} func={setWidthB} error={false}/>
  }

  return (
    <>
      <StyledConfigurationStartTitle>Введите размеры кухни</StyledConfigurationStartTitle>
      <StyledConfigurationStartRow>
        <SizeInputItem name={widthWallAName} value={widthA} func={setWidthA} error={false}/>
        <SizeInputItem name='Высота' value={height} func={setHeight} error={false}/>
        <SizeInputItem name='Глубина' value={depth} func={setDepth} error={false}/>
        <SizeInputItem name='Фартук' value={apron} func={setApron} error={false}/>
      </StyledConfigurationStartRow>
      <StyledConfigurationStartRow marginTop='25px'>
        {widthWallBView}
        <div style={{width: '300px'}}></div>
        <div style={{width: '300px'}}></div>
        <div style={{width: '300px'}}></div>
      </StyledConfigurationStartRow>
    </>
  );
};

export default SizeInput;