import { useEffect, useMemo } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { setModulesCommon, setWidth } from './../../../../actions/index';

import useCalc from '../../../../service/CalcWidth';
import ConfigurationParametrs from './../../../configurationParametrs/ConfigurationParametrs';
import ModuleParameters from './../../../moduleParameters/ModuleParameters';
import ConfigurationMap from './../../../configurationMap/ConfigurationMap';

import styled from 'styled-components';
import './configurationLayout.scss';

import StyledConteiner from './../../../styledGlobal/StyledConteiner';

import { bgSvg } from '../../../../resources/img/configurationLayout';

import pImg01 from './../../../listModules/img01.svg';
import pImg02 from './../../../listModules/img02.svg';
import pImg022 from './../../../listModules/img022.svg';
import pImg03 from './../../../listModules/img03.svg';
import pImg04 from './../../../listModules/img04.svg';
import pImg05 from './../../../listModules/img05.svg';
import pImg06 from './../../../listModules/img06.svg';
import pImg07 from './../../../listModules/img07.svg';
import pImg08 from './../../../listModules/img08.svg';
import pImg01Active from './../../../listModules/img01-active.svg';
import pImg02Active from './../../../listModules/img02-active.svg';
import pImg022Active from './../../../listModules/img022-active.svg';
import pImg03Active from './../../../listModules/img03-active.svg';
import pImg04Active from './../../../listModules/img04-active.svg';
import pImg05Active from './../../../listModules/img05-active.svg';
import pImg06Active from './../../../listModules/img06-active.svg';
import pImg07Active from './../../../listModules/img07-active.svg';
import pImg08Active from './../../../listModules/img08-active.svg';

const StyledConfigurationLayout = styled.section`
  position: relative;
`
const StyledConfigurationLayoutPlan = styled.div`
  position: relative;
  background: url(${bgSvg});
  padding: 30px 0 0;
  margin: 35px 0;
  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 35px;
    background: #fff;
  }
`
const StyledConfigurationLayoutPlanContent = styled(StyledConteiner)`
  position: relative;
`

const ConfigurationLayout = () => {
  const {width, widthA, widthB, wall, configurationMainPage } = useSelector(state => state);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setModulesCommon([
      {
        'startId': 1,
        'id': 1,
        'quanity': 0,
        'name': 'Верхний шкаф',
        'text': 'Одна дверь',
        'img': pImg02,
        'activeImg': pImg02Active,

        'type': 'up',
        'width': 40,
        'appointment': 'storage',

        'indent': 0
      },
      // {
      //   'id': 2,
      //   'quanity': 0,
      //   'name': 'Верхний ящик',
      //   'text': 'Одна дверь',
      //   'img': pImg01,
      //   'activeImg': pImg01Active,

      //   'type': 'up',
      //   'width': 40,
      //   'appointment': 'storage',

      //   'indent': 0,
      // },
      {
        'startId': 2,
        'id': 2,
        'quanity': 0,
        'name': 'Нижний шкаф',
        'text': 'Одна дверь',
        'img': pImg022,
        'activeImg': pImg022Active,

        'type': 'down',
        'width': 40,
        'appointment': 'storage',

        'replaceMax': 3,
        'indent': 0,
      },
      {
        'startId': 3,
        'id': 3,
        'quanity': 0,
        'name': 'Нижний шкаф',
        'text': 'Двойная дверь',
        'img': pImg06,
        'activeImg': pImg06Active,

        'type': 'down',
        'width': 60,
        'appointment': 'storage',

        'replaceMin': 2,
        'indent': 0,
      },
      {
        'startId': 4,
        'id': 4,
        'quanity': 0,
        'name': 'Нижняя угловая',
        'text': 'Левый',
        'img': pImg07,
        'activeImg': pImg07Active,

        'type': 'down',
        'width': 60,
        'appointment': 'storage',

        'indent': 0,
      },
      {
        'startId': 5,
        'id': 5,
        'quanity': 0,
        'name': 'Нижняя угловая',
        'text': 'правый',
        'img': pImg08,
        'activeImg': pImg08Active,

        'type': 'down',
        'width': 60,
        'appointment': 'storage',

        'indent': 0,
      },
      {
        'startId': 7,
        'id': 7,
        'quanity': 0,
        'name': 'Под духовку',
        'text': 'Описание',
        'img': pImg03,
        'activeImg': pImg03Active,

        'type': 'down',
        'width': 80,
        'appointment': 'technic',

        'indent': 24,
      },
      {
        'startId': 8,
        'id': 8,
        'quanity': 0,
        'name': 'Под мойку',
        'text': 'Описание',
        'img': pImg04,
        'activeImg': pImg04Active,

        'type': 'down',
        'width': 80,
        'appointment': 'washing',

        'indent': 39,
      },
    ]))
    dispatch(setWidth(widthA))
  }, [])

  useEffect(() => {
    if (wall === 1) dispatch(setWidth(widthA));
    else dispatch(setWidth(widthB));
  }, [wall])

  const { calcWidthPercent } = useCalc();

  const sizeWidth = useMemo(() => calcWidthPercent(width), [width, configurationMainPage]);

  return (
    <>
      <main>
        <StyledConfigurationLayout>
          <ConfigurationParametrs />
          <ModuleParameters />
          <StyledConfigurationLayoutPlan
            // onMouseUp={(e) => onSetResizeWidthEnd(e)} 
            // onMouseMove={(e) => onResizeWidth(e, sizeWidth)}
            >
            <StyledConfigurationLayoutPlanContent>
              <ConfigurationMap sizeWidth={sizeWidth}/>
            </StyledConfigurationLayoutPlanContent>
          </StyledConfigurationLayoutPlan>
        </StyledConfigurationLayout>
      </main>
    </>
  );
};

export default ConfigurationLayout;