import styled from 'styled-components';

import NavCalc from './../../../nav/navCalc/NavCalc';
import TableCalcDetails from './../../../table/tableCalcDetails/TableCalcDetails';
import TableCalcSheet from './../../../table/tableCalcSheet/TableCalcSheet';

import StyledButton from './../../../button/Button';
import StyledConteiner from './../../../styledGlobal/StyledConteiner';

const StyledConfigurationCalc = styled.section`
 padding: 60px 0 120px;
`
const StyledConfigurationCalcTitleBlock = styled.div`
  margin: 60px 0 0;
`
const StyledConfigurationCalcContent = styled(StyledConteiner)`
  display: flex;
  justify-content: space-between;
  align-items: center;
`
const StyledConfigurationCalcText = styled.div`
  font-weight: 400;
  font-size: 14px;
  line-height: 115%;
  color: #000000;
`
const StyledConfigurationCalcName = styled.div`
  font-weight: 600;
  font-size: 28px;
  line-height: 115%;
  color: #15293E;
  margin: 12px 0 0;
`
const StyledConfigurationCalcBtnBlock = styled.div`
  text-align: right;
`

const ConfigurationCalc = () => {


  return (
    <>
      <main>
        <StyledConfigurationCalc>
          <NavCalc />
          <StyledConfigurationCalcTitleBlock>
            <StyledConfigurationCalcContent>
              <div>
                <StyledConfigurationCalcText>Название проекта</StyledConfigurationCalcText>
                <StyledConfigurationCalcName>Прямая кухня</StyledConfigurationCalcName>
              </div>
              <div>
                <StyledButton cutting >Произвести раскрой</StyledButton>
              </div>
            </StyledConfigurationCalcContent>
          </StyledConfigurationCalcTitleBlock>
          <TableCalcDetails />
          <TableCalcSheet />
          <StyledConfigurationCalcBtnBlock>
            <StyledConteiner>
              <StyledButton cutting>Произвести раскрой</StyledButton>
            </StyledConteiner>
          </StyledConfigurationCalcBtnBlock>
        </StyledConfigurationCalc>
      </main>
    </>
  );
};

export default ConfigurationCalc;