import { useSelector, useDispatch } from 'react-redux';
import { setCurrentModuleSelected, setModulesSelectedDown, setModulesSelectedDown2, setModulesSelectedUp, setModulesSelectedUp2 } from './../actions/index';

import useCalc from './CalcWidth';

const useResize = () => {
  const {currentModuleSelected, wall, modulesSelectedDown, modulesSelectedUp, modulesSelectedUp2, modulesSelectedDown2, modulesSelectedMiddle, width, heightU, heightD} = useSelector(state => state);
  const dispatch = useDispatch();

  const { calcWidthAll, calcWidthPercentItem } = useCalc();

  const _defineListAndFunctions = () => {
    let modulesSelected, setModulesSelected;
    if (currentModuleSelected.type === 'up') {
      if (wall === 1) {
        modulesSelected = modulesSelectedUp;
        setModulesSelected = setModulesSelectedUp;
      }
      else {
        modulesSelected = modulesSelectedUp2;
        setModulesSelected = setModulesSelectedUp2;
      }
    } else if (currentModuleSelected.type === 'down') {
      if (wall === 1) {
        modulesSelected = modulesSelectedDown;
        setModulesSelected = setModulesSelectedDown;
      }
      else {
        modulesSelected = modulesSelectedDown2;
        setModulesSelected = setModulesSelectedDown2;
      }
    }
    return {modulesSelected, setModulesSelected}
  }

  const _updateValue = (field, value, func, arr) => {
    const newCurrentModuleSelected = {...currentModuleSelected};
    newCurrentModuleSelected[`${field}`] = value;
    dispatch(setCurrentModuleSelected({...newCurrentModuleSelected}))
    dispatch(func(
      [...arr.map(el => {
        if (el.id === currentModuleSelected.id)
          return {...newCurrentModuleSelected}
        return el
      })]
    ))
  }

  const onResizeWidth = (value, update=false) => {
    let newValue = value;
    if (newValue < 200) 
      newValue = 200; 
    else if (newValue > 800) 
      newValue = 800; 

    const {modulesSelected, setModulesSelected} = _defineListAndFunctions();

    const widthAll = calcWidthAll(modulesSelected, modulesSelectedMiddle);
    if (widthAll + (newValue - currentModuleSelected.width) > width)
      if (newValue >= currentModuleSelected.width)
        newValue = width - widthAll + currentModuleSelected.width;

    if (update || (!update && value === newValue))
      _updateValue('width', newValue, setModulesSelected, modulesSelected)

    return newValue;
  }

  const onResizeHeigth = (value, type, update=true) => {
    const {modulesSelected, setModulesSelected} = _defineListAndFunctions();

    
    let newValue = value;
    console.log('1-heightU', heightU, newValue);
    if (type === 'up') {
      if (newValue > heightU) newValue = heightU;
      else if (newValue < 200) newValue = 200;
    } else {
      if (newValue > heightD) newValue = heightD;
      else if (newValue < 200) newValue = 200;
    }

    console.log('2-heightU', heightU, newValue);

    if (update)
      _updateValue('height', newValue, setModulesSelected, modulesSelected)

    return newValue;
  }

  const onResizeDepth = (value) => {
    const {modulesSelected, setModulesSelected} = _defineListAndFunctions();

    _updateValue('depth', value, setModulesSelected, modulesSelected)

    return value;
  } 

  // const onResizeWidth = (value, change=false) => {   
  //   let vList = 0;
  //   let eTarget = document.querySelector('.configuration-layout-plan-selected__item-w-down.active .resize-w-btn');
  //   if (eTarget === null) {
  //     vList = 1;
  //     eTarget = document.querySelector('.configuration-layout-plan-selected__item-w-down-2.active .resize-w-btn');
  //   }

  //   let formatNewItem = Number(value);

  //   // let check = 0;
  //   // if (formatNewItem < 200) {
  //   //   if (formatNewItem <= currentModuleSelected.width) {
  //   //     check = 1;
  //   //     formatNewItem = 200;
  //   //   } 
  //   // }
  //   // else if (formatNewItem > 800) {
  //   //   if (formatNewItem >= currentModuleSelected.width) {
  //   //     check = 1;
  //   //     formatNewItem = 800;
  //   //   }
  //   // }

  //   const allModulesWidth1 = calcWidthAll(modulesSelectedDown);
  //   const allModulesWidth2 = calcWidthAll(modulesSelectedDown2);

  //   if (vList === 0) { 
  //     if (allModulesWidth1 + (formatNewItem - currentModuleSelected.width) > width) {
  //       if (formatNewItem >= currentModuleSelected.width) {
  //           formatNewItem = width - allModulesWidth1 + currentModuleSelected.width;
  //         }
  //     }
  //   } else {
  //     if (allModulesWidth2 + (formatNewItem - currentModuleSelected.width) > width) {
  //       if (formatNewItem >= currentModuleSelected.width) {
  //         formatNewItem = width - allModulesWidth2 + currentModuleSelected.width;
  //       }
  //     }
  //   }

  //   if (change === false || (change === true && check !== 1)) {
  //     dispatch(setCurrentModuleSelected(
  //       {...currentModuleSelected, 
  //         width: formatNewItem,
  //       }
  //     ))

  //     if (vList === 0) {
  //       dispatch(setModulesSelectedDown(
  //         [...modulesSelectedDown.map((el) => {
  //           if (el.id === currentModuleSelected.id)
  //             return {...currentModuleSelected, 
  //                       width: formatNewItem,
  //                     }
  //           else {
  //             return el
  //           }
  //         })
  //         ]
  //       ))
  //     } else {
  //       dispatch(setModulesSelectedDown2(
  //         [...modulesSelectedDown2.map((el) => {
  //           if (el.id === currentModuleSelected.id)
  //             return {...currentModuleSelected, 
  //                       width: formatNewItem,
  //                     }
  //           else {
  //             return el
  //           }
  //         })
  //         ]
  //       ))
  //     }
  //   }
  //   return formatNewItem;
  // }

  return {onResizeWidth, onResizeHeigth, onResizeDepth, _updateValue}
};

export default useResize;