import { useState, useEffect, useRef } from 'react';

import { useDispatch } from 'react-redux';

import styled, { css } from "styled-components";

import { selectSvg } from './../../resources/img/select';

const StyledSelect = styled.div`
  margin-top: ${props => props.margin || '16px'};
`
const StyledSelectTitle = styled.div`
  color: #8F97A3;
  font-weight: 500;
  ${props => props.configurationSettings && css`
    font-size: 14px;
    line-height: 20px;
  `}
  ${props => props.moduleParameters && css`
    font-size: 12px;
    line-height: 14px;
  `}
`
const StyledSelectCurrent = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  width: 100%;
  background: #f1f2f4;
  border-radius: 4px;
  margin-top: ${props => props.margin || '6px'};
  cursor: pointer;
`
const StyledSelectText = styled.div`
  color: #393E46;
  font-weight: 400;
  opacity: 0.9;
  ${props => props.configurationSettings && css`
    font-size: 14px;
    line-height: 20px;
  `}
  ${props => props.moduleParameters && css`
    font-size: 12px;
    line-height: 14px;
  `}
`
const StyledSelectImg = styled.div`
  width: 13px;
  height: 9px;
  img {
    width: 100%;
    height: 100%;
  }
`
const StyledSekectList = styled.ul`
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 99999;
`
const StyledSelectItem = styled.li`
  width: 100%;
  background: #F1F2F4;
  padding: 8px 12px;
`

const Select = ({name, current, funcSelection, list, ...props}) => {
  const dispatch = useDispatch();

  const ref = useRef();

  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    const checkIfClickedOutside = e => {
      if (isOpen && ref.current && !ref.current.contains(e.target))
        setIsOpen(false)
    }
    document.addEventListener("mousedown", checkIfClickedOutside);
    return () => {
      document.removeEventListener("mousedown", checkIfClickedOutside)
    }
  }, [isOpen])

  const renderList = () => {
    const arr = list.map(item => {
      if (item !== current)
        return (
          <StyledSelectItem key={item} onClick={() => dispatch(funcSelection(item))}>
            <StyledSelectText props={props}>{item}</StyledSelectText>
          </StyledSelectItem>
        )
    });
    return (<StyledSekectList>{arr}</StyledSekectList>)
  }

  const listView = isOpen ? renderList() : null; 

  return (
    <StyledSelect 
      ref={ref}
      margin={props.blockMarginTop}>
      <StyledSelectTitle props={props}>{name}</StyledSelectTitle>
      <StyledSelectCurrent 
        onClick={() => setIsOpen(isOpen => !isOpen)}
        margin={props.CurrentMarginTop}>
        <StyledSelectText props={props}>{current}</StyledSelectText>
        <StyledSelectImg><img src={selectSvg} alt="Выбрать" /></StyledSelectImg>
        {listView}
      </StyledSelectCurrent>
    </StyledSelect>
  );
};

export default Select;