import { useEffect, useCallback, useRef, useMemo } from 'react';

import { useDispatch, useSelector } from 'react-redux';

import { setResizeActiveModuleWidth, setCurrentModuleSelected, setModulesSelectedDown } from './../../../actions/index';

import useResize from './../../../service/Resize';


const ListModulesPartResize = ({item}) => {
  const { currentModuleSelected, resizeActiveModuleWidth } = useSelector(state => state);
  const dispatch = useDispatch();

  const ref = useRef(null);

  const {onResizeWidth} = useResize();

  const mouseDown = (e, id) => {
    e.preventDefault();
    dispatch(setResizeActiveModuleWidth(id));
  }

  const mouseMove = useCallback(
    (e) => {
      if (resizeActiveModuleWidth !== false && ref) {

        const cordMouse = e.pageX;
        const cordModule = ref.current.parentElement.offsetLeft;
        const cordNull = ref.current.parentElement.parentElement.offsetLeft;

        const elList = ref.current.parentElement.parentElement.parentElement

        const cordList1 = elList.parentElement.parentElement.parentElement.offsetLeft
        const cordList2 = elList.parentElement.parentElement.offsetLeft
        const cordList3 = elList.parentElement.offsetLeft
        const cordList4 = elList.offsetLeft

        const width1 = (cordMouse - cordList1 - cordList2 - cordList3 - cordList4 - cordModule - cordNull + 17 + 52) * 400 / 90;
        const width = Math.trunc(width1 / 10) * 10;

        onResizeWidth(width, true)
      }
    }, [resizeActiveModuleWidth, ref]
  );

  const removeListeners = useCallback(() => {
    window.removeEventListener("mousemove", mouseMove);
    window.removeEventListener("mouseup", removeListeners);
  }, [mouseMove]);

  const mouseUp = useCallback(() => {
    dispatch(setResizeActiveModuleWidth(false));
    removeListeners();
  }, [setResizeActiveModuleWidth, removeListeners]);

  useEffect(() => {
    if (resizeActiveModuleWidth !== false) {
      window.addEventListener("mousemove", mouseMove);
      window.addEventListener("mouseup", mouseUp);
    }

    return () => {
      removeListeners();
    };
  }, [resizeActiveModuleWidth, mouseMove, mouseUp, removeListeners]);

  return (
    <div
      ref={ref}
      className={`resize-w ${item.id === currentModuleSelected?.id ? 'active':''}`}>
      <span></span>
      <span>{item.width} мм</span>
      <span></span>
      <span
        onMouseDown={(e) => mouseDown(e, item.id)}
        className="resize-w-btn"></span>
    </div>
  );
};

export default ListModulesPartResize;