import { useSelector } from 'react-redux';

import styled, { css } from 'styled-components';

import { onSvg } from '../../../resources/img/configurationStart';

const StyledStyledConfigurationItem = styled.li`
  cursor: pointer;
`
const StyledStyledConfigurationImg = styled.div`
  position: relative;
  width: 300px;
  height: 200px;
  border: 1px solid #F7F7F7;
  background: #F7F7F7;
  ${props => props.active && css`
    border: 1px solid #689A56;
    background: #A8CD9B;
    &::after {
      content: url(${onSvg});
      position: absolute;
      top: -12px;
      right: -12px;
      width: 24px;
      height: 24px;
      padding: 4px 7px;
      background: #689A56;
      border-radius: 50%;
      z-index: 1;
    }
  `}
  img {
    display: block;
    padding: 40px;
    margin: 0 auto;
  }
`
const StyledStyledConfigurationName = styled.p`
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: ${props => props.active?'#689A56':'#212121'};
  margin: 15px 0 0;
`

const ConfigurationСhoiceItem = ({name, img, imgActive, onSetConfiguration}) => {
  const {configuration} = useSelector(state => state);
  
  const active = configuration === name ? 'active' : false;

  return (
    <StyledStyledConfigurationItem onClick={() => onSetConfiguration(name)}>
      <StyledStyledConfigurationImg active={active}>
        <img src={configuration===name?imgActive:img} alt="Конфигурация" />
      </StyledStyledConfigurationImg>
      <StyledStyledConfigurationName active={active}>{name}</StyledStyledConfigurationName>
    </StyledStyledConfigurationItem>
  );
};

export default ConfigurationСhoiceItem;