
import styled from 'styled-components';

import { cuttingSvg, detailsSvg } from '../../../resources/img/configurationCalc';

const StyledConfigurationCalcNav = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 22px 20px;
  width: 424px;
  height: 64px;
  border-radius: 10px;
  box-shadow: 0px 0px 40px 1px rgba(0, 0, 0, 0.1);
  background: #ffffff;
  margin: 0 auto;
`
const StyledConfigurationCalcNavBtn = styled.div`
  display: flex; 
  align-items: center;
  opacity: ${props => props.opacity || 0.4};
  cursor: pointer;
  img {
    margin: 0 6px 0 0;
  }
`
const StyledConfigurationCalcNavText = styled.div`
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #15141A;
`
const StyledConfigurationCalcNavLine = styled.span`
  width: 40px;
  height: 1px;
  background: rgba(21, 20, 26, 0.1);
`

const NavCalc = () => {
  return (
    <StyledConfigurationCalcNav>
      <StyledConfigurationCalcNavBtn opacity={1}>
        <img src={detailsSvg} alt="Деталировка" />
        <StyledConfigurationCalcNavText>Деталировка</StyledConfigurationCalcNavText>
      </StyledConfigurationCalcNavBtn>
      <StyledConfigurationCalcNavLine />
      <StyledConfigurationCalcNavBtn>
        <img src={cuttingSvg} alt="Раскрой" />
        <StyledConfigurationCalcNavText>Карта раскроя</StyledConfigurationCalcNavText>
      </StyledConfigurationCalcNavBtn>
    </StyledConfigurationCalcNav>
  );
};

export default NavCalc;