import { useEffect, useRef, useCallback, useState } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { setResizeActiveModuleHeigth } from './../../actions/index';

import useCalc from './../../service/CalcWidth';
import useResize from './../../service/Resize';

const ResizeHeight = ({type}) => {
  const { currentModuleSelected, resizeActiveModuleHeigth } = useSelector(state => state);
  const dispatch = useDispatch();

  const ref = useRef(null);

  const { calcWidthPercentItem } = useCalc();
  const { onResizeHeigth } = useResize();

  const [point, setPoint] = useState();

  // useEffect((value) => {
  //   onResizeHeigth(h);
  // }, [h])

  const mouseDown = (e, id) => {
    e.preventDefault();
    setPoint(ref.current.getBoundingClientRect().y);
    dispatch(setResizeActiveModuleHeigth(id));
  }

  const mouseMove = useCallback(
    (e) => {
      if (resizeActiveModuleHeigth !== false && ref) {
        const cordMouse = e.clientY;
        // console.log(cordMouse, e.target);
        const cordItem = ref.current.getBoundingClientRect();

        // point
        // let heigth = (cordItem.y - cordMouse) * 400 / 85;
        let heigth = (point - cordMouse) * 400 / 85;
        heigth = Math.trunc(heigth / 10) * 10;

        // setH(h => h - heigth)
        if (heigth !== 0) {
          console.log(currentModuleSelected.height, heigth, cordItem.y, cordMouse);
          onResizeHeigth(currentModuleSelected.height + heigth, 'down');
        }
      }
    }, [resizeActiveModuleHeigth, ref]
  );

  const removeListeners = useCallback(() => {
    window.removeEventListener("mousemove", mouseMove);
    window.removeEventListener("mouseup", removeListeners);
  }, [mouseMove]);

  const mouseUp = useCallback(() => {
    dispatch(setResizeActiveModuleHeigth(false));
    removeListeners();
  }, [setResizeActiveModuleHeigth, removeListeners]);

  useEffect(() => {
    if (resizeActiveModuleHeigth !== false) {
      window.addEventListener("mousemove", mouseMove);
      window.addEventListener("mouseup", mouseUp);
    }

    return () => {
      removeListeners();
    };
  }, [resizeActiveModuleHeigth, mouseMove, mouseUp, removeListeners]);

  const calcHeight = calcWidthPercentItem(currentModuleSelected.height);

  return (
    <div
      ref={ref} 
      style={{height: `${calcHeight}px`}}
      className={`resize-h ${currentModuleSelected?.type === type ? 'active':''}`}>
      <span></span>
      <span style={{width: '45px'}}>{currentModuleSelected.height} мм</span>
      <span></span>
      <span
        style={{
          display: 'block',
          width: '10px',
          height: '10px',
          background: 'red',
          transform: 'translate(-4px, 0)',
          margin: '0 auto',
          cursor: 'pointer',
        }}
        onMouseDown={(e) => mouseDown(e, currentModuleSelected.id)}
        className="resize-w-btn"></span>
    </div>
  );
};

export default ResizeHeight;