import { useMemo } from 'react';

import { useSelector } from 'react-redux';

import styled from 'styled-components';

import useCalc from '../../service/CalcWidth';

import { arrow } from '../../resources/img/configurationLayout';

const StyledWidthLine = styled.div`
  width: calc(${props => props.width}% + 12px);
  margin: 0 0 0 50px;
`
const StyledWidthLineNum = styled.div`
  display: block;
  text-align: center;
  font-size: 12px;
  line-height: 14px;
  color: #15141A;
`
const StyledWidthLineRow = styled.div`
  display: flex;
  align-items: center;
`
const StyledWidthLineArrow = styled.div`
    width: 12px;
    height: 20px;
    img {
      width: 100%;
      height: 100%;
      display: cover;
    }
`
const StyledWidthLineArrowLeft = styled(StyledWidthLineArrow)`
  transform: rotate(90deg);
`
const StyledWidthLineArrowRight = styled(StyledWidthLineArrow)`
  transform: rotate(-90deg);
`
const StyledWidthLineLine = styled.div`
  display: block;
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.25);
` 

const PlanWidth = () => {
  const { width } = useSelector(state => state);
  
  const { calcWidthPercent } = useCalc();

  const sizeWidth = useMemo(() => calcWidthPercent(width), [width]);

  return (
    <StyledWidthLine width={sizeWidth} >
      <StyledWidthLineNum className="configuration-layout-plan__width-line-num">{width} мм</StyledWidthLineNum>
      <StyledWidthLineRow className="configuration-layout-plan__width-line-line">
        <StyledWidthLineArrowLeft><img src={arrow} alt="<-" /></StyledWidthLineArrowLeft>
        <StyledWidthLineLine />
        <StyledWidthLineArrowRight><img src={arrow} alt="->" /></StyledWidthLineArrowRight>
      </StyledWidthLineRow>
    </StyledWidthLine>
  );
};

export default PlanWidth;