import { useState, useEffect } from 'react';

import { useSelector } from 'react-redux';

import ModuleParametersSizeItem from './../moduleParametersSizeItem/ModuleParametersSizeItem';

import useResize from './../../../service/Resize';

import { StyledParametersModuleBlock } from '../ModuleParameters';

const ModuleParametersSize = () => {
  const { currentModuleSelected } = useSelector(state => state);

  const [localWidthItem, setLocalWidthItem] = useState(currentModuleSelected.width); 
  const [localHeightItem, setLocalHeightItem] = useState(currentModuleSelected.height); 
  const [localDepthItem, setLocalDepthItem] = useState(currentModuleSelected.depth); 
  const [localBorderItem, setLocalBorderItem] = useState(0); 

  const {onResizeWidth, onResizeHeigth, onResizeDepth} = useResize();

  useEffect(() => {
    setLocalWidthItem(currentModuleSelected.width);
    setLocalHeightItem(currentModuleSelected.height);
    setLocalDepthItem(currentModuleSelected.depth);
  }, [
    currentModuleSelected,
    currentModuleSelected.width,
    currentModuleSelected.depth
  ])

  const onChangelocalWidth = (value) => {
    setLocalWidthItem(value);
    onResizeWidth(Number(value));
  }

  const onChangeGlobalWidth = (code) => {
    if (code === 'Enter' || code === 'NumpadEnter')
      setLocalWidthItem(onResizeWidth(Number(localWidthItem), true));
  }

  const onChangelocalheight = (value) => {
    setLocalHeightItem(value)
    onResizeHeigth(Number(value), currentModuleSelected?.type==='down'?'down':'up', false);
  }

  const onChangeGlobalheight = (code) => {
    if (code === 'Enter' || code === 'NumpadEnter')
      setLocalHeightItem(onResizeHeigth(Number(localHeightItem), currentModuleSelected?.type==='down'?'down':'up'));
  }

  const onChangelocalDepth = (value) => {
    setLocalDepthItem(value);
    onResizeDepth(Number(value));
  }

  const onChangeGlobalDepth = (code) => {
    if (code === 'Enter' || code === 'NumpadEnter')
      setLocalDepthItem(onResizeDepth(Number(localDepthItem)));
  }

  return (
    <StyledParametersModuleBlock>
      <span className="parameters-Module-body__name bold">Размеры</span>
      <div className="parameters-Module-body__row row-6">
        <ModuleParametersSizeItem 
          name='Ширина' 
          lovalValue={localWidthItem} 
          localFuncChange={onChangelocalWidth} 
          globalFuncChange={onChangeGlobalWidth} />
        <ModuleParametersSizeItem 
          name='Высота' 
          lovalValue={localHeightItem} 
          localFuncChange={onChangelocalheight} 
          globalFuncChange={onChangeGlobalheight} />
      </div>
      <div className="parameters-Module-body__row row-10">
        <ModuleParametersSizeItem 
            name='Глубина'
            lovalValue={localDepthItem}
            localFuncChange={onChangelocalDepth} 
            globalFuncChange={onChangeGlobalDepth} />
        <ModuleParametersSizeItem 
            name='Передняя граница'
            lovalValue={localBorderItem}
            localFuncChange={() => console.log('undefined')} 
            globalFuncChange={() => console.log('undefined')} />
      </div>
    </StyledParametersModuleBlock>
  );
};

export default ModuleParametersSize;