import { useMemo } from 'react';

import { useSelector } from 'react-redux';

import styled from 'styled-components';

import useCalc from './../../service/CalcWidth';

import { arrow } from "../../resources/img/configurationLayout";

const StyledWidthLine = styled.div`
  height: ${props => props.height}px;
  position: relative;
  margin: -14px 20px 0 0;
`
const StyledHeightLineNum = styled.div`
  position: absolute;
  top: 50%;
  left: 0;
  align-items: center;
  font-size: 12px;
  line-height: 14px;
  color: #15141A;
  transform: translate(0, -50%);  
  letter-spacing: -3.5px;
  word-spacing: -5px;
  white-space: nowrap;
  writing-mode: vertical-lr; 
  text-orientation: upright; 
`
const StyledHeightLineColumn = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin: 0 0 0 20px;
`
const StyledWidthLineArrow = styled.div`
  width: 12px;
  height: 20px;
  img {
    width: 100%;
    height: 100%;
    display: cover;
  }
`
const StyledHeightLineArrowTop = styled(StyledWidthLineArrow)`
  transform: rotate(-180deg);
`
const StyledHeightLineArrowBottom = styled(StyledWidthLineArrow)`
`
const StyledHeightLineLine = styled.div`
  display: block;
  width: 1px;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
` 

const PlanHeight = () => {
  const { height } = useSelector(state => state);
  
  const { calcWidthPercentItem } = useCalc();

  const sizeHeight = useMemo(() => calcWidthPercentItem(height), [height]);

  return (
    <StyledWidthLine height={sizeHeight}>
      {/* <StyledHeightLineRow className="configuration-layout-plan__width-line-line"> */}
        <StyledHeightLineNum className="configuration-layout-plan__width-line-num">{height} мм</StyledHeightLineNum>
        <StyledHeightLineColumn>
          <StyledHeightLineArrowTop><img src={arrow} alt="Вверх" /></StyledHeightLineArrowTop>
          <StyledHeightLineLine />
          <StyledHeightLineArrowBottom><img src={arrow} alt="Вниз" /></StyledHeightLineArrowBottom>
        </StyledHeightLineColumn>
      {/* </StyledHeightLineRow> */}
    </StyledWidthLine>
  );
};

export default PlanHeight;